import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useEffect, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
// form
import { Controller, useForm} from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, FormControl, Grid, InputLabel, MenuItem, Select, Stack } from '@mui/material';
import axios from 'axios';
// utils
// import { fData } from '../../../utils/formatNumber';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
// assets
import { countries } from '../../../assets/data';
// components
// import Label from '../../../components/label';
import { useSnackbar } from '../../../components/snackbar';
import FormProvider, {
  RHFSelect,
  RHFTextField,
} from '../../../components/hook-form';
import { HOST_API_LOCAL, END_POINT_CREAR_AREA, END_POINT_COMBO_LISTA_EMPRESA, END_POINT_EDITAR_AREA } from '../../../config-global';
import GenericCombo from '../../../utils/GenericCombo';
import Iconify from '../../../components/iconify';


// ----------------------------------------------------------------------

AreaNewEditForm.propTypes = {
  isEdit: PropTypes.bool,
  currentArea: PropTypes.object,
};

export default function AreaNewEditForm({ isEdit = false, currentArea }) {
  const navigate = useNavigate();

  const { enqueueSnackbar } = useSnackbar();

  const NewAreaSchema = Yup.object().shape({
      nombre: Yup
    .string()
    .required('Ingrese nombre del área'),
      empresaId: Yup
     .string()
     .required('Ingrese la empresa'),
    // rol: Yup
    // .string()
    // .required('Seleccione empleado'),

  });

    const [defaultValues, setDefaultValues] = useState({
        nombre: currentArea?.nombre || '',
        empresaId: currentArea?.empresaId
    })
   

  const methods = useForm({
    resolver: yupResolver(NewAreaSchema),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;


  const [mostrarBotonEditar, setMostrarBotonEditar] = useState(isEdit);

  const [disabled, setDisabled] = useState(isEdit);

  // const values = watch();

    useEffect(() => {
        if (isEdit && currentArea) {
            reset(currentArea);
        }
    }, [isEdit, currentArea, reset, setDefaultValues]);

  const onSubmit = async (data) => {
    try {
      // await new Promise((resolve) => setTimeout(resolve, 500));
      // reset();
      // enqueueSnackbar(!isEdit ? '¡Su area fue creada!' : '¡Sus cambios fueron actualizados!');
      // navigate(PATH_DASHBOARD.area.list);
        console.log('DATA', data);
        const form = new FormData();
        const urlEndPoint = isEdit ? HOST_API_LOCAL + END_POINT_EDITAR_AREA : HOST_API_LOCAL + END_POINT_CREAR_AREA;
        const AUT = `Bearer ${localStorage.getItem('accessToken')}`
        Object.entries(data).map(([key, value]) => form.append(key, value));

        console.log(form)

        axios({
            method: 'post',
            url: urlEndPoint,
            data: form,
            accept: 'application/json',
            contentType: false,
            procesData: false,
            headers: { 'Authorization': AUT }
        }).then(response => {
            console.log(response)

            if (response.statusText === "OK")
                enqueueSnackbar(!isEdit ? '¡Su area fue creada!' : '¡Sus cambios fueron actualizados!');
            else
                enqueueSnackbar(response.data);
        });


    } catch (error) {
      console.error(error);
    }
  };

    const [age, setAge] = useState('');

    const handleChange = (event) => {
        setAge(event.target.value);
    };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={12}>
          <Card sx={{ p: 3 }}>
            <Box
              rowGap={3}
              columnGap={3}
              display="grid"
              gridTemplateColumns={{
                xs: 'repeat(1, 1fr)',
                sm: 'repeat(3, 1fr)',
              }}
            >
                          <RHFTextField disabled={disabled} name="nombre" label="Nombre del área" />
                          
                          <GenericCombo
                              disabled={disabled}
                              nameAUX="empresaId"
                              label="Empresa"
                              placeholder="Empresa"
                              endPointURL={END_POINT_COMBO_LISTA_EMPRESA}
                              onChangeFunc={() => { } }
                          />

                        
                         


                { /* <RHFSelect native name="EmpresaId" label="Empresa" placeholder="Empresa"> */ }
                { /*    <option value="" /> */ }
                { /*    {combo.map((item) => ( */ }
                { /*        <option key={item.id} value={item.id}> */ }
                { /*            {item.razonSocial} */ }
                { /*        </option> */ }
                { /*    ))} */ }
                { /* </RHFSelect> */ }

              { /* <RHFSelect native name="rol" label="Empleado" placeholder="Empleado"> */ }
              { /*   <option value="" /> */ }
              { /*   {countries.map((country) => ( */ }
              { /*     <option key={country.code} value={country.label}> */ }
              { /*       {country.label} */ }
              { /*     </option> */ }
              { /*   ))} */ }
              { /* </RHFSelect> */ }
             </Box>

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                          {mostrarBotonEditar ?
                              null :
                              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                                  {!isEdit ? 'Crear Area' : 'Guardar Cambios'}
                              </LoadingButton>
                          }

                          {mostrarBotonEditar ?
                              <LoadingButton onClick={() => { setDisabled(false); setMostrarBotonEditar(false); }}>
                                  <Iconify icon="eva:edit-fill" /> Editar
                              </LoadingButton> :
                              null
                          }
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
